.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.button {
  padding-left: 10px;
  padding-right: 10px;
 color: blue
}
.button.active {
  color: red
}
img {
  width: 200px;
  height: 200px;
}
.menu {
  display: inline-flex;
  justify-content: space-around;
  text-align: center;
}
.logout{
  position: fixed;
  /* bottom: 0; */
  right: 0;
  width: 85px;
  margin: 1%;
  
}